
import './index.css'
import loader from '@/loader/loader'
import config from './config'
import apis from './mock'

import Header from './components/header'
import CameraDis from './components/cameraDis'
import SystemNav from './components/systemNav'

loader.load({
  apis: apis,
  config: config,
  init() {
    const header = new Header('.header') // 顶部
    const cameraDis = new CameraDis('.pages-left') // 摄像头分布 
    const systemNav = new SystemNav('.pages-right') // 导航
    header.render()
    cameraDis.render()
    systemNav.render()
  }
})



