import './index.css'
import $ from 'jquery'
import { fetch } from '@/util/request'
import hbs from './templates/index.hbs'
import listHbs from './templates/list.hbs'

export default class CameraDis {
  constructor(el) {
    $(el).append(hbs)
  }
  render() {
    // 摄像头分布情况
    fetch('fetchCameraDis', data => {
      // 总数
      $('.count .value').text(data.cameraDis.total)
      // 地区列表
      $('.count-list').html(listHbs(data.cameraDis.list))
    })
  }
} 



