export default {
  'url': 'eyesight/home/cameraDis',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'cameraDis': {
        'total|1000-20000': 1, 
        'list|10': [
          {
            'name|+1': ['江北', '沙坪坝', '渝北', '渝中', '南岸', '九龙坡', '大渡口', '北碚', '巴南', '璧山'],
            'value|1-3000': 1 
          }
        ]
      }
    } 
  }
} 




