import './index.css'
import $ from 'jquery'
import { fetch, fetchInterval, fetchSocket } from '@/util/request'
import hbs from './index.hbs'

export default class Header {
  constructor(el) {
    $(el).append(hbs)
  }

  /**
   *  渲染
   *  @return   {void}  
   */
  render() {
    const self = this
    // 在线人数
    fetch('fetchOnline', data => {
      $('.online span').text(`${data.online}人`)
    })

    // 用户名
    fetch('fetchUser', data => {
      $('.username').text(data.user)
    })
    self.renderTime()
    self.bindEvent()
  }

  /**
   *  渲染时间
   *  @return   {object}  null
   */
  renderTime() {
    const self = this
    // 地本时间
    setInterval(() => {
      self.getCurrentDate()
    }, 6000)

    // 调用服务器时间
    // self.getServerDate()
  }

  /**
   *  获取服务器时间
   *  @return   {object}  null
   */
  getServerDate() {
    // 服务器时间(线上把这里换成fetchSocket)
    fetchSocket('fetchTimewebsocket', 1, data => {
      let date = JSON.parse(data.data) 
      date = date.result
      const year = date.slice(0, 4)
      const month = date.slice(4, 6)
      const day = date.slice(6, 8)
      const housr = date.slice(8, 10)
      const minutes = date.slice(10, 12)
      let time = `${year}.${month}.${day}`
      $('.timer-date').text(time)
      $('.timer-hour').text(`${housr}:${minutes}`)
    })
  }

  /**
   *  获取本地时间
   *  @return   {void}   
   */
  getCurrentDate() {
    const self = this
    const date = new Date()
    const year = date.getFullYear()
    const month = self.formateDate(date.getMonth() + 1) 
    const day = self.formateDate(date.getDate())
    const housr = self.formateDate(date.getHours())
    const minutes = self.formateDate(date.getMinutes())
    let time = `${year}.${month}.${day}`
    $('.timer-date').text(time)
    $('.timer-hour').text(`${housr}:${minutes}`)
  }

  /**
   *  格式化时间
   *  @param    {number}  t 时间字符串
   *  @return   {string}  时间格式
   */
  formateDate (t) {
    if (t < 10) {
      return `0${t}`
    }
    return t
  }

  /**
   *  事件绑定
   *  @example: [example]
   *  @return   {void} 
   */
  bindEvent() {
    // 退出系统
    $('.out-system').on('click', () => {
      fetch('fetchLogout', (data) => {
        window.location = data.logout
      })
    })
  }
} 




