import './index.css'
import $ from 'jquery'
import d3 from 'd3'
import hbs from './index.hbs'
import system from './systemNav.json'
const data = system.system

export default class SystemNav {
  constructor(el) {
    
    $(el).append(hbs(data))
    const pie = d3.layout.pie()
      .value(() => 1)
      .sort(null)
    
    this.pieData = pie(data)
    // 创建弧生成器
    this.arc = d3.svg.arc()
      .innerRadius(200)
      .outerRadius(580)  
  }

  /**
   *  渲染
   *  @return   {void}  
   */
  render() {
    const self = this
    let posi = []
    d3.select('.navs-list')
      .selectAll('a')
      .each((d, i) => {
        let p = self.arc.centroid(self.pieData[i]) 
        posi.push(p)
      })
    // 改变left, top位置   
    data.map((d, i) => {
      $('.navs-list').find('a').eq(i)
        .css('left', posi[i][0] - 226 / 2)
        .css('top', posi[i][1] - 225 / 2)
    })
  }
} 




